<template>
  <div class="ba-needs-section">
    <div class="ba-needs-section__inner row column">
      <div class="row" v-if="hosps">
        <div class="ba-section-filter column small-16 medium-8 large-5">
          <div class="ba-section-filter__body">
            <h3 class="ba-section-filter__title">{{filters.title}}</h3>
            <div class="ba-section-filter__subtitle">{{filters.subtitle}}</div>
            <div class="ba-section-filter__title-label">Показувати:</div>
            <div class="ba-section-filter__filter-wrap">
              <label class="ba-section-filter__radio" v-for="(item, i) in filtersHosps" :key="i">
                <input
                  v-model="filter"
                  type="radio"
                  name="category"
                  :value="item.value"
                  :checked="item.checked"
                  data-filter-map
                />
                <span>{{item.title}} ({{item.numbers}})</span>
              </label>
            </div>
            <div class="ba-section-filter__title-label">Позначення:</div>
            <div
              class="ba-section-filter__marker"
              v-for="(item, index) in filters.marker"
              :key="index"
            >
              <svg :style="{fill: `${filters.marker[index].color}`}">
                <use xlink:href="@/assets/img/sprite.svg#marker" />
              </svg>
              {{filters.marker[index].description}}
            </div>
          </div>
        </div>
        <Map :filter="filter" :hospitals="hospitals" />
      </div>
    </div>
  </div>
</template>

<script>
import Map from "@/components/home/Map.vue";
export default {
  props: ["needs", "filters"],
  data() {
    return {
      hosps: null,
      filter: "all",
      hospitals: [],
      filtersHosps: [
        {
          title: "Опорні лікарні",
          numbers: "",
          value: "oporni-likarni",
          checked: false,
        },
        {
          title: "Інші лікарні ",
          numbers: "",
          value: "inshi",
          checked: false,
        },
        { title: "ЗСУ", numbers: "", value: "zsu", checked: false },
        { title: "Всі лікарні", numbers: "", value: "all", checked: true },
      ],
    };
  },
  name: "Needs",
  components: {
    Map,
  },
  created() {
    fetch("https://razomua.flywheelstaging.com/wp-json/hospitals/v1/post")
      .then((result) => result.json())
      .then((data) => {
        this.hosps = data;
        this.hospitals = this.hosps.filter((t) => {
          if (t.types.length > 0) {
            return t;
          } else return;
        });
        this.oporni = this.hosps.filter((t) => {
          if (t.types.length > 0) {
            return t.types[0].slug === "oporni-likarni";
          } else return;
        });
        this.inshi = this.hosps.filter((t) => {
          if (t.types.length > 0) {
            return t.types[0].slug === "inshi";
          } else return;
        });
        this.zsu = this.hosps.filter((t) => {
          if (t.types.length > 0) {
            return t.types[0].slug === "zsu";
          } else return;
        });
        this.filtersHosps[1].numbers = this.inshi.length;
        this.filtersHosps[0].numbers = this.oporni.length;
        this.filtersHosps[2].numbers = this.zsu.length;
        this.filtersHosps[3].numbers = this.hosps.length;
      });
  },
};
</script>

<style lang="scss">
.ba-needs-section {
  background: $light-gray;
  display: flex;
  @include breakpoint(xlarge) {
    display: flex;
  }
}
.ba-section-filter {
  padding: 62px 16px 42px;
  @include breakpoint(medium) {
    padding: 80px 40px 63px;
  }
  @include breakpoint(large) {
    padding: 64px 16px 36px 72px;
  }
  &__title {
    margin: 0;
  }
  &__subtitle {
    color: $medium-gray;
    margin-bottom: 40px;
  }
  &__filter-wrap {
    margin-bottom: 40px;
  }
  &__radio {
    line-height: 1;
    display: flex;
    align-items: center;

    span {
      display: inline-flex;
      align-items: center;
      &::before {
        content: "";
        display: inline-block;
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        flex-grow: 0;
        border: 1px solid $medium-gray;
        border-radius: 50%;
        margin-right: 10px;
      }
      &:hover::before {
        background-color: $medium-gray;
        box-shadow: inset 0 0 0 2px $white;
      }
    }
    input:active + span::before,
    input:checked + span::before {
      background-color: $primary-color;
      box-shadow: inset 0 0 0 2px $white;
    }
    & > input {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }
    & + & {
      margin-top: 16px;
    }
  }
  &__title-label {
    color: $medium-gray;
    margin-bottom: 24px;
  }
  &__marker {
    display: flex;
    align-items: center;
    & + & {
      margin-top: 16px;
    }
    svg {
      width: 16px;
      height: 20px;
      margin-right: 10px;
    }
  }
}
</style>