import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../pages/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Головна',
    component: Home
  },
  {
    path: '/potreby-likaren',
    name: 'Потреби лікарень',
    component: () => import('../pages/NeedsOfHospitals.vue')
  },
  {
    path: '/vikonano',
    name: 'Hospitals',
    component: () => import('../pages/Hospitals.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
