<template>
  <div class="ba-section-map column small-16 medium-16 large-11 medium-collapse">
    <div class="google-map" ref="googleMap"></div>
    <template
      class="ba-map"
      apiKey="AIzaSyAm9CHZaUpHbSF3q18i-hQzQxfmaYkttNc"
      v-if="Boolean(this.google) && Boolean(this.map)"
    >
      <div></div>
    </template>
  </div>
</template>

<script>
import { mapSettings } from "@/constants/mapSettings.js";
import GoogleMapsApiLoader from "google-maps-api-loader";

export default {
  data() {
    return {
      markers: [],
      google: null,
      map: null,
      apiKey: "AIzaSyAm9CHZaUpHbSF3q18i-hQzQxfmaYkttNc",
    };
  },
  props: ["hosps", "filter", "hospitals", "mapConfig"],
  async created() {
    const googleMapApi = await GoogleMapsApiLoader({
      apiKey: this.apiKey,
    });
    this.google = googleMapApi;
    this.initializeMap();
    this.setMarker();
  },

  methods: {
    initializeMap() {
      console.log(11, this.google);

      const mapContainer = this.$refs.googleMap;
      this.map = new this.google.maps.Map(mapContainer, this.mapConfig);
    },
    setMarker() {
      this.hospitals.forEach((element) => {
        const marker = new this.google.maps.Marker({
          position: {
            lat: +element.acf.google_map.lat,

            lng: +element.acf.google_map.lng,
          },
          map: this.map,
          category: element.types[0].slug,
          icon: {
            path:
              "M16 0c-5.523 0-10 4.477-10 10 0 10 10 22 10 22s10-12 10-22c0-5.523-4.477-10-10-10zM16 16c-3.314 0-6-2.686-6-6s2.686-6 6-6 6 2.686 6 6-2.686 6-6 6z",
            anchor: new google.maps.Point(16, 30),
            size: new google.maps.Size(16, 20),
            strokeOpacity: 1,
            strokeWeight: 1,
            strokeColor: element.acf.help,
            fillColor: element.acf.help,
            fillOpacity: 1,
            scale: 0.8,
          },
        });
        //  console.log(position.lat);

        this.markers.push(marker);
        // console.log(markers);
      });

      if (
        this.filter == "all" ||
        this.filter == "inshi" ||
        this.filter == "zsu" ||
        this.filter == "oporni-likarni"
      ) {
        //   console.log(this.filter);

        let newMarker = [];
        for (let i = 0; i < this.markers.length; i++) {
          let mark = this.markers[i];
          console.log(mark.category);
          console.log(this.filter);

          if (mark.category == this.filter) {
            mark.setVisible(true);
            newMarker.push(mark);
          }
          //  else {
          //    mark.setVisible(false);
          //  }
        }
      }
    },
  },
  computed: {
    mapConfig() {
      return {
        ...mapSettings,
        center: { lat: 49.0139, lng: 31.2858 },
      };
    },
  },
};
</script>


<style lang="scss">
.google-map {
  width: 100%;
  min-height: 100%;
}
.ba-section-map {
  @include breakpoint(medium down) {
    padding: 0;
  }
}
.ba-map {
  height: 260px;
  @include breakpoint(medium) {
    height: 640px;
  }
}
</style>